<template>
    <ListLayout>
        <div class="m-archive-box" v-loading="loading">
            <!--  -->
            <div class="m-archive-search" slot="search-before">
                <a :href="publish_link" class="u-publish el-button el-button--primary">+ </a>
                <el-input placeholder="" v-model.trim.lazy="search">
                    <span slot="prepend"><i class="el-icon-search"></i> <span class="u-search"></span></span>
                    <el-button slot="append" icon="el-icon-position" class="u-btn"></el-button>
                </el-input>
            </div>

            <!--  -->
            <div class="m-archive-filter">
                <div class="m-filter--left">
                    <!--  -->
                    <!-- <clientBy @filter="filterImperceptibly" :type="client"></clientBy> -->

                    <!--  -->
                    <el-radio-group class="u-type" v-model="tag" size="mini">
                        <el-radio-button label=""></el-radio-button>
                        <el-radio-button label="PVE">PVE</el-radio-button>
                        <el-radio-button label="PVP">PVP</el-radio-button>
                    </el-radio-group>

                    <!--  -->
                    <markBy @filter="filterMeta"></markBy>
                    <!--  -->
                    <zlpBy @filter="filterMeta" type="zlp" :client="client"></zlpBy>
                    <!--  -->
                    <topicBy v-model="topic" :topics="topics" />
                </div>
                <div class="m-filter--right">
                    <!--  -->
                    <orderBy @filter="filterMeta"></orderBy>
                </div>
            </div>

            <!--  -->
            <div class="m-archive-list" v-if="data && data.length">
                <ul class="u-list">
                    <list-item v-for="(item, i) in data" :key="i + item" :item="item" :order="order"  />
                </ul>
            </div>

            <!--  -->
            <el-alert v-else class="m-archive-null" title="" type="info" center show-icon></el-alert>

            <!--  -->
            <el-button
                class="m-archive-more"
                v-show="hasNextPage"
                type="primary"
                @click="appendPage"
                :loading="loading"
                icon="el-icon-arrow-down"
                ></el-button
            >

            <!--  -->
            <el-pagination
                class="m-archive-pages"
                background
                layout="total, prev, pager, next, jumper"
                :hide-on-single-page="true"
                :page-size="per"
                :total="total"
                :current-page.sync="page"
                @current-change="changePage"
            ></el-pagination>
        </div>
    </ListLayout>
</template>
<script>
import ListLayout from "@/layout/ListLayout.vue";
import { appKey } from "@/../setting.json";
import listItem from "@/components/list/list_item.vue";
import { publishLink } from "@jx3box/jx3box-common/js/utils";
import { getPosts, getTopicPost } from "@/service/post";
import post_topics from "@jx3box/jx3box-common/data/post_topics.json";
export default {
    name: "Index",
    props: [],
    data: function () {
        return {
            loading: false, //
            data: [], //

            page: 1, //
            per: 10, //
            total: 1, //
            pages: 1, //
            number_queries: ["per", "page"],

            subtype: "", //
            order: "update", //
            mark: "", //
            client: this.$store.state.client, //
            search: "", //
            zlp: "", //
            tag: "", //
            topic: "", // 

            pv_types: ["PVE", "PVP"],
        };
    },
    computed: {
        // 
        publish_link: function () {
            return publishLink(appKey);
        },
        // 
        hasNextPage: function () {
            return this.pages > 1 && this.page < this.total;
        },
        // 
        query: function () {
            return {
                subtype: this.subtype,
                order: this.order,
                mark: this.mark,
                client: this.client,
                search: this.search,
                zlp: this.zlp,
                tag: this.tag,

                topic: this.topic,
            };
        },
        // 
        pg_queries: function () {
            return {
                page: this.page,
                per: this.per,
            };
        },
        // 
        reset_queries: function () {
            return [this.subtype, this.search];
        },
        topics: function ({ tag }) {
            if (tag === 'PVE') {
                return post_topics['bps_pve']
            }
            if (tag === 'PVP') {
                return post_topics['bps_pvp']
            }
            return [...new Set([...post_topics['bps_pve'], ...post_topics['bps_pvp']])]
        },
    },
    methods: {
        // 
        buildQuery: function (appendMode) {
            if (appendMode) {
                this.page += 1;
            }
            let _query = {
                per: this.per,
                page: this.page,
            };

            for (let key in this.query) {
                if (this.query[key] !== undefined && this.query[key] !== "" && this.query[key] !== null) {
                    if (key == "search") {
                        _query.search = this.query.search.trim();
                    } else {
                        _query[key] = this.query[key];
                    }
                }
            }
            // 
            if (_query.subtype) {
                _query.sticky = 1;
            }

            return _query;
        },
        // 
        loadData: function (appendMode = false) {
            let query = this.buildQuery(appendMode);
            // console.log("[cms-list]", "<loading data>", query);

            const fn = query?.topic ? getTopicPost : getPosts;

            this.loading = true;
            return fn(query)
                .then((res) => {
                    if (appendMode) {
                        this.data = this.data.concat(res.data?.data?.list);
                    } else {
                        this.data = res.data?.data?.list;
                    }
                    this.total = res.data?.data?.total;
                    this.pages = res.data?.data?.pages;
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        // 
        replaceRoute: function (extend) {
            return this.$router
                .push({ name: this.$route.name, query: Object.assign({}, this.$route.query, extend) })
                .then(() => {
                    window.scrollTo(0, 0);
                })
                .catch((err) => {});
        },
        // 
        filterMeta: function (o) {
            this.replaceRoute({ [o["type"]]: o["val"], page: 1 });
        },
        // 
        filterImperceptibly: function (o) {
            this[o["type"]] = o["val"];
        },
        // 
        changePage: function (i) {
            this.loadData();
            this.replaceRoute({ page: i });
        },
        // 
        appendPage: function () {
            this.loadData(true);
        },
    },
    watch: {
        // 
        "$route.query": {
            deep: true,
            immediate: true,
            handler: function (query) {
                if (Object.keys(query).length) {
                    // console.log("[cms-list]", "<route query change>", query);
                    for (let key in query) {
                        // for:element
                        if (this.number_queries.includes(key)) {
                            this[key] = ~~query[key];
                        } else {
                            this[key] = query[key];
                        }
                    }
                }
            },
        },
        // 
        reset_queries: {
            deep: true,
            handler: function () {
                // console.log("[cms-list]", "<reset page>");
                this.page = 1;
            },
        },
        // 
        query: {
            deep: true,
            immediate: true,
            handler: function (query) {
                // console.log("[cms-list]", "<request query change>", query);
                this.loadData();
            },
        },
    },
    mounted: function () {},
    components: {
        listItem,
        ListLayout,
    },
};
</script>

<style lang="less">
@import "~@/assets/css/list.less";
</style>
